/* eslint-disable react/prefer-stateless-function */
/* eslint-disable react/prop-types */
import React from 'react';
import CKEditor from '@ckeditor/ckeditor5-react';
import BallonBlockEditor from '@ckeditor/ckeditor5-build-balloon-block';

class CKEditorWidget extends React.Component {
  controlID = `rich-editor-${Math.random()}`;

  componentDidMount() {
    const { field, onOpenMediaLibrary } = this.props;

    const t = onOpenMediaLibrary({
      controlID: this.controlID,
      forImage: true,
      privateUpload: field.get('private'),
      value: null,
      allowMultiple: false,
      config: field.get('media_library', Map()).get('config'),
      field,
    });
    debugger;
  }

  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
      value,
      onChange,
    } = this.props;
    return (
      <div
        className={classNameWrapper}
        style={{
          paddingLeft: 10,
          paddingRight: 10,
          minHeight: 200,
        }}
      >
        <CKEditor
          id={forID}
          editor={BallonBlockEditor}
          config={{
            blockToolbar: [
              'heading',
              '|',
              'alignment',
              'bulletedList',
              'numberedList',
              '|',
              'indent',
              'outdent',
              '|',
              'imageUpload',
              'blockQuote',
              'insertTable',
              'mediaEmbed',
              '|',
              'undo',
              'redo',
            ],
            toolbar: {
              items: ['bold', 'italic', 'link'],
            },
          }}
          data={value}
          onChange={(event, editor) => {
            const data = editor.getData();
            onChange(data);
          }}
          onBlur={() => setInactiveStyle()}
          onFocus={() => setActiveStyle()}
          style={{ outline: 'none' }}
        />
      </div>
    );
  }
}

export default CKEditorWidget;
